.card {
  width: 270px;
  min-height: 320px;
  max-height: 320px;
  /* margin-left: 40px;
    margin-right: 40px;  */
  margin: 20px;
  /* display: flex;
    flex-direction: row; */
    box-shadow: none;
}
.card:hover{
  /* background-color:#fafaed; */
  box-shadow: 1px 1px 40px #cfcfcf;
  cursor: pointer;
  -webkit-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;
  }
  /* @keyframes popout {
    from{transform:scale(0)}
    30%{transform:scale(0.75)}
    to{transform:scale(0.5)}
} */


.card-img {
  width: auto;
  max-width: 300px;
  height: auto;
  max-height: 170px;
  min-height: 170px;
}
.card-body {
  align-content: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 14px;
  order: 1;
  outline-color: #fafaed;
}

.card-subtitle {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-align: center;
  font-size: 13px;
  padding-top: 5%;
}

.eventName {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: pre-line;
  font-size: 15px;
  padding: 8px;
  height: 3.8em;
  text-align: left;
  font-weight: 400
}
